// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import vueResource from 'vue-resource'
import Main from './components/Main'
import About from './components/About'
import Contact from './components/Contact'

Vue.use(VueRouter)
Vue.use(vueResource)

const router = new VueRouter({
	mode: 'history',
	base: __dirname,
	routes: [
		{path: '/', component: Main},
		{path: '/about', component: About},
		{path: '/contact', component: Contact}
	]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  router,
  template: `

  	<div id="app">
  		<nav class="blue lighten-1">
	    <div class="nav-wrapper container blue lighten-1">
	    <a href="#" class="brand-logo left hide-on-med-and-down">MRCEO</a>
	    <a href="#" data-activates="mobile-nav" class="button-collapse"><i class="fa fa-bars"></i> MENU</a>
	      <ul id="nav-mobile" class="right hide-on-med-and-down">
	        <li><router-link to="/">Home</router-link></li>
	        <li><router-link to="/about">About</router-link></li>
	        <li><router-link to="/contact">Contact</router-link></li>
	      </ul>
	      <ul class="side-nav" id="mobile-nav" style="background-color:transparent; color: #42A5F5">
	        <li><router-link style="color: #42A5F5" to="/">Home</router-link></li>
	        <li><router-link style="color: #42A5F5" to="/about">About</router-link></li>
	        <li><router-link style="color: #42A5F5" to="/contact">Contact</router-link></li>
	      </ul>
	    </div>
	  </nav>
	  <router-view></router-view>
  	</div>

  `
}).$mount('#app')

$( document ).ready(function(){
	$(".button-collapse").sideNav();
})


