<template>
  <div class="contact-page container">
    <h3>{{header}}</h3>
    <div class="liner"></div>
    <div class="row">
      <div class="col s12 m12 l12 contact-info">
        <div class="card">
            <div class="card-content white-text">
              <h6 class="card-title center"><i class="fa fa-address-book-o"></i> Contact Information</h6>
              <ul class="social-links">
                <li><a href="#"><i class="fa fa-facebook-official fa-3x blue-text text-lighten-3"></i></a></li>
                <li><a href="#"><i class="fa fa-twitter-square fa-3x"></i></a></li>
                <li><a href="#"><i class="fa fa-instagram fa-3x pink-text"></i></a></li>
                <li><a href="#"><i class="fa fa-github-square fa-3x black-text"></i></a></li>
              </ul>
              <p class="flow-text center">
                <span class=""><i class="fa fa-whatsapp green-text fa-2x"></i> 08135782355</span><br>
                <span class=""><i class="fa fa-envelope-open-o orange-text text-darken-4 fa-2x"></i> Alieyuib@gmail.com</span>
              </p>
             <div><br>
                <p class="copy center"><i class="fa fa-copyright"></i> 2018 All Rights | Reserved {{footerName}}</p>
             </div>
            </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'contact-page',
  data () {
    return {
      header: 'Contact Me',
      footerName: 'MRCEO'
    }
  },
  methods:{
    
  },
  created: function () {
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  h3{
    text-transform: uppercase;
    color: #fff;
    padding-top: 50px;
  }

  .liner{
    width: 30%;
    border-bottom: 5px solid #42A5F5;
  }

  p.copy{
    /*margin-top: 50px;*/
  }

  .card{
    background-color: rgba(0,0,0,0.4);
    border: 1px solid rgba(66,165,245, 0.2);
    margin-top: 80px;
    padding: 80px;
    /*text-align: center;
    height: 450px;*/
  }

  .card h6{
    font-weight: bold;
  }

  .card h6 i{
    color: #42A5F5;
    font-style: normal;
  }

  .card p{
    font-size: 16px;
  }

  ul.social-links{
    width: 62%;
    display: block;
    margin-left: 130px;
    padding: 30px;
    background-color: rgba(66,165,245, 0.2);
  }

  ul.social-links li{
    display: inline-block;
    position: relative;
    margin-left: 40px;
  }

  @media (max-width: 667px){
    .card{
    margin-top: 80px;
    padding: 0px;
  }
  .card h6{
    font-weight: bold;
    width: 100%;
    padding: 0;
  }
  ul.social-links{
    width: 100%;
    display: block;
    margin-left: 0;
    background-color: rgba(66,165,245, 0.2);
  }
  ul.social-links li{
    display: inline-block;
    position: relative;
    margin-left: 15px;
  }
  p.flow-text{
    display: block;
    text-align: center !important;
  }
  }

</style>



